/*************************************
***** NEPO Block in Colors ***** 
*************************************/

.nepo {
	display: inline-block;
	&::before {
		content: "";
		display: inline-block;
		width: rem(85px);
		height: rem(20px);
		background-image: url("img/nepo.svg");
		background-position: left bottom;
		background-repeat: no-repeat;
		background-size: contain;
		margin-right: rem(5px);
	}
}

/*************************************
***** News ***** 
*************************************/

.teaser-news {
    display: flex;
    background: $gray-200;
    padding: rem(15px);
    @include media-breakpoint-up(lg) {
        padding: rem(40px);
    }
    border-radius: $border-radius--standard;
    &--header {
        font-weight: $bold;
        @extend .font-size--md;
        margin-bottom: rem(10px);
    }
    &--body {
        h3 {
            color: $primary;
        }
    }
	&--image {
		width: 100%;
		height: auto;
	}
}

/*************************************
***** Footer ***** 
*************************************/

#start footer {
    border-top: rem(1px) solid rgba($black,.2);
    // padding-top: rem(40px);
    padding-bottom: rem(96px); //40+96px from Header
    width: 100%;
	img {
		width: 100%;
	}
    p,
    .nav-link  {
        font-size: rem(14px);
        line-height: rem(20px);
		margin-right: 0;
    }
    .nav-item {
        background: none;
        &:hover {
            background: none;
        }
    }
    .nav-link {
        padding: rem(5px 0);
        color: $gray-600;
    }
}